<template>
    <audio id="jun-audio" controls="controls" hidden="true" v-if="status === 'on' && show">
      <source :src="url" type="audio/ogg">
      <source :src="url" type="audio/mpeg">
    </audio>
</template>

<script>
// @ is an alias to /src
import { mapGetters } from 'vuex'

export default {
  name: 'home',
  components: {
  },
  data () {
    return {
      status: this.$store.getters.audioSwitch
    }
  },
  model: {
    prop: 'params',
    event: 'change'
  },
  props: {
    show: {
      type: [Boolean],
      default: true
    },
    url: {
      default: ''
    },
    id: {
      type: String,
      default: 'audio'
    },
    params: [Object]
  },
  computed: {
    ...mapGetters([
      'audioSwitch'
    ])
  },
  watch: {
    audioSwitch (val) {
      this.status = val
      this.setParams(val)
    }
  },
  created () {
  },
  mounted () {
    this.setParams(this.status)
  },
  methods: {
    setParams (status) {
      this.$nextTick(() => {
        const myAudio = document.querySelector('#jun-audio')
        const obj = {
          el: myAudio || null,
          status,
          play () {
            this.el && myAudio.play()
            !this.el && console.warn('System sound is off')
          },
          stop () {
            myAudio && myAudio.pause()
          },
          reload () {
            myAudio && myAudio.load()
          }
        }
        this.$emit('change', obj)
      })
    }
  }
}
</script>

